<template>
	<left-right>
		<div slot="left">
			<core-data ref="coreData"></core-data>
		</div>
	</left-right>
</template>
<style lang="scss">
	tr {
		background: red;
	}
	input[type="checkbox"] {
		disabled: true;
	}
</style>
<script>
	import leftRight from '@/components/LeftRight.vue'
	import { poll, getFeatureData, getFeatureList } from '@/api/api'
	import coreData from '@/components/CoreData'
	const values = ['未知', '连续', '离散', '时间'];

	export default {
		components: {
			leftRight,
			coreData
		},
		data () {
			return {
				fListId: this.$route.query.fLId,
				targetId: this.$route.query.targetId,
				projectId: this.$route.params.projectId,
				taskId: this.$route.params.taskId
			}
		},
		methods: {
			/**
			 * 
			 */
			pollTrainTask (projectId,  interval) {
				const progressItems = this.$store.state.progressItems;
				let dataLoaded = false;
				const timer = setInterval ( () => {
					progressItems.length = 0;
					poll(projectId).then(data => {
						let { error, training_task } = data;
						if (error) {
							this.$message.error('');
							clearInterval(timer);
							return;
						}
						const trainingTask = training_task;
						console.log(trainingTask);
						if (trainingTask) {
							const { stages, train_info } = trainingTask;
							console.log(train_info);
							// 特征重要性已经出来了
							if (train_info && train_info.preprocess_finished && !dataLoaded) {
								console.log('before params');
								const params = {
									project_id: this.projectId,
									train_task_id: this.taskId
								};
								console.log(this.$refs.coreData.init);
								dataLoaded = true;

								this.$refs.coreData.init(train_info.feature_list_id, params);
								this.$refs.coreData.showImportance();
								//this.init(train_info.feature_list_id, params);
							}

							progressItems.length = 0;
							for (let i = 0, len = stages.length; i < len; i++) {
								progressItems.push({
									name: (i + 1) + '.' + stages[i].stage_name,
									percent: stages[i].percentage + '%',
        							status: stages[i].percentage == 100 ? 2 : 1,
        							duration: '2.5s',
        							failReason: ''
								})
							}
							// 下一步
							progressItems.push({
								name: '准备中...',
								status: 0
							})
						}
						const status = trainingTask.status;
						const uploadProgress = this.$store.state.uploadProgress;
						
						// 上传数据
						switch (status) {
							case 0:
								break; 
							case 1:
								break;
							case 2:
								break; 
							case 3:
								
								break;
							// 训练任务结束
							case 4: 
								progressItems.pop();
								// 模型也可以点击了
								this.$store.commit('SET_MODEL_STATUS', true);
								alert('模型能点啦！！并会添加浮夸的样式！');
								clearInterval(timer);
								break;
							case 5: 
								clearInterval(timer);
								
							break;
								break;
						}
					}).catch((error) => {
						console.log(error);
						clearInterval(timer);
					})
				}, interval);
			}
		},
		mounted () {
			this.$store.state.progressItems.length = 0;
			this.pollTrainTask(this.projectId, 500);
			this.$store.commit('SET_TRAIN_STATUS', false);
		}
	}
</script>






